import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { createNewBlog } from '../reducers/blogReducer'
import { createNotification } from '../reducers/notificationReducer'

const BlogForm = (props) => {
  const [author, setAuthor] = useState('')
  const [title, setTitle] = useState('')
  const [url, setUrl] = useState('')

  const addBlog = async (event) => {
    try {
      event.preventDefault()

      const newBlog = {
        title: this.state.title,
        author: this.state.author,
        url: this.state.url
      }

      this.props.createNewBlog(newBlog)

      this.props.createNotification({ message: `A new blog ${this.state.title} by ${this.state.author} added` })

      this.setState({
        title: '',
        author: '',
        url: '',
      })


    } catch (ex) {
      this.setState({
        error: `couldn't add new blog due to an error: ${ex}`
      })
      setTimeout(() => {
        this.setState({ error: null })
      }, 3000)
    }
  }

  return (
    <div>
      <h2>Kirjaudu</h2>

      <form onSubmit={addBlog}>
        <div>
          title
          <input
            type="text"
            name="title"
            value={title}
            onChange={({ target }) => setTitle(target.value)}
          />
        </div>
        <div>
          author
          <input
            type="text"
            name="author"
            value={author}
            onChange={({ target }) => setAuthor(target.value)}
          />
        </div>
        <div>
          url
          <input
            type="text"
            name="url"
            value={url}
            onChange={({ target }) => setUrl(target.value)}
          />
        </div>
        <button>create</button>
      </form>
    </div>
  )
}

BlogForm.propTypes = {
  title: PropTypes.string.isRequired,
  author: PropTypes.string.isRequired,
  url: PropTypes.string.isRequired
}

export default connect(
  null,
  { createNewBlog, createNotification }
)(BlogForm)
